<template>
  <view class="body">
    <image class="banner" src="../../static/volunteer/banner.jpg" mode="widthFix"></image>
    <view class="content">
      <view class="title">【填写前必读规则】</view>
      <view class="report">
        <view>恶意刷数据举报给客服</view>
        <button open-type="contact">点击跳转客服</button>
      </view>
      <view class="text">分享给身边考研同学和考研群</view>
      <view class="text">人越多,数据越准,避免报名扎堆</view>
      <view class="title">【严格保证数据真实性】</view>
      <view class="list">
        <view class="cell import">1、一个手机号只能保留一个有效成绩！</view>
        <view class="cell">2、上限10次,只保留最新成绩,
          <view class="import">恶意刷数据封号！</view>
        </view>
      </view>
    </view>
    <button class="statistics" v-if="data.total==0" @click="toComplete">志愿填报统计</button>
    <button class="statistics" v-else @click="toResult">查看统计结果</button>
  </view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { volunteer } from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				data:{}
			}
		},
		onShareAppMessage() {
			//分享
			wx.showShareMenu({
		      withShareTicket: true,
		      menus: ['shareAppMessage', 'shareTimeline']
		     })
		    return {
			  title: '考研调剂平台小程序',
		      path: '/pages/volunteer/volunteer',
			  imageUrl:'/static/review/help_share.jpg', //设置分享图片
		      // promise 
		    }
		},
		onShareTimeline: function () {
			//分享监听事件
			return {
			  title: '	考研调剂平台小程序',
			}
		},
		onLoad() {
			
		},
		onShow() {
		  this.token = store.state.vuex_token
		  volunteer({token:this.token}).then(res=>{
		  	console.log(res)
			this.data=res.data.data
		  }).catch(err=>{
		  	console.log(err)
		  })
		},
		methods: {
		  toComplete() {
			  uni.navigateTo({
				url:'/pages/volunteer/complete'
			  })
		  },
		  toResult() {
			  uni.navigateTo({
				url:'/pages/volunteer/result'
			  })
		  },
		}
    }
</script>

<style lang="scss">
  .body{
      width: 100vw;
      min-height: 100vh;
      background-color: #f7f6fb;
      padding-bottom: 30rpx;
      .banner{
        width: 100vw;
      }
      .content{
        margin: 20rpx 5%;
        background-color: #fff;
        border-radius: 20rpx;
        padding: 30rpx 20rpx;
        font-size: 30rpx;
        line-height: 52rpx;
        .title{
          font-size: 34rpx;
          font-weight: 600;
          margin: 20rpx 0;
          margin-left: -12rpx;
        }
        .title:first-child{
          margin-top: 0;
        }
        .report{
          color: #028c66;
          display: flex;
          align-items: center;
          button{
            margin-left: 30rpx;
            background-color: #028c66;
            color: #fff;
            font-size: 26rpx;
            padding: 8rpx 20rpx;
            border-radius: 50rpx;
            line-height: 30rpx;
          }
        }
        .text{
          color: #ff3284;
        }
        .list{
          line-height: 48rpx;
          .cell{
            font-size: 26rpx;
          }
          .import{
            display: inline-block;
            color: #ff0000;
          }
        }
      }
      .statistics{
        width: 90%;
        margin: 50rpx auto;
        border-radius: 50rpx;
        line-height: 88rpx;
        font-size: 30rpx;
        color: #fff;
        text-align: center;
        background-color: #0884f9;
      }
  }
</style>